import AppInput from './AppInput'
import canvas from './Canvas'
import touchInput from './TouchInput'

var mouseInput = { };

const doubleClickIntervalMS = 300;
const dragMinDist = 4;

var clickTimer = null;
var mdown = false;
var moved = false;
var initialX = null;
var initialY = null;
var savedZoom = 1.0;

var pointerLoc = {x: null, y: null};

function dist(x1, y1, x2, y2) {
    var dx = x1 - x2;
    var dy = y1 - y2;
    return Math.sqrt(dx*dx + dy*dy);
}

mouseInput.pointerLocation = () => {
  return pointerLoc;
}

mouseInput.attachEvents = function(canvasElement) {
    canvasElement.addEventListener('wheel', (e) => {
        e.preventDefault();
        if (e.ctrlKey) {
          var zm = canvas.getZoom();
          var newval  = Math.floor(zm * 100 - e.deltaY);
          if (newval >= 5) {
              canvas.zoomAt(e.pageX, e.pageY, newval / 100);
          }        
        } else {
          canvas.moveDelta(-e.deltaX, -e.deltaY);
        }
    });
      
    canvasElement.addEventListener("gesturestart", function (e) {
        e.preventDefault();
        if (touchInput.inProgress()) {
          return;
        }
        savedZoom = window.Module.getZoom();
    });
      
    canvasElement.addEventListener("gesturechange", function (e) {
        e.preventDefault();
        if (touchInput.inProgress()) {
          return;
        }
        var newval = Math.round(savedZoom * e.scale * 100) / 100;
        canvas.zoomAt(e.pageX, e.pageY, newval);
    });
      
    canvasElement.addEventListener("gestureend", function (e) {
        e.preventDefault();
    });
  
    window.bean.on(canvasElement, 'click', function (e) {
        e.preventDefault();
        if (e.button == 2) {
          AppInput.contextmenu(e);
          return;
        }
        if (e.button != 0) {
          return;
        }
        if (moved) {
          moved = false;
          return;
        }
  
        if (clickTimer != null) {
            clearTimeout(clickTimer);
            clickTimer = null;
            AppInput.dbclick(e.offsetX, e.offsetY);
        } else {
            clickTimer = setTimeout(function () {
                clickTimer = null;
                AppInput.click(e.offsetX, e.offsetY);
            }, doubleClickIntervalMS);
        }
    });
  
    window.bean.on(canvasElement, 'mousedown', function (e) {
        if (e.button != 0) {
          return;
        }
        initialX = e.offsetX;
        initialY = e.offsetY;
        mdown = true;
    });
  
    window.bean.on(canvasElement, 'mouseup', function (e) {
        if (e.button == 0) {
          mdown = false;
          if (moved) {
            AppInput.drag(e.offsetX, e.offsetY, 2);
          }
        }
    });
  
    window.bean.on(canvasElement, 'mousemove', function (e) {
        e.preventDefault();
        // update pointer location
        pointerLoc.x = e.offsetX;
        pointerLoc.y = e.offsetY;
        
        if (mdown && e.button != 0) {
          return;
        }
  
        if (!mdown) {
          AppInput.move(e.offsetX, e.offsetY);
          return;
        }
        if (moved) {
          AppInput.drag(e.offsetX, e.offsetY, 1);
        } else {
          if (dist(initialX, initialY, e.offsetX, e.offsetY) >= dragMinDist) {
            moved = true;
            AppInput.drag(initialX, initialY, 0);
            AppInput.drag(e.offsetX, e.offsetY, 1);
          }
        }
    });
  
    window.bean.on(canvasElement, 'mouseenter', function (e) {
        AppInput.enter(e.offsetX, e.offsetY);
    });
  
    window.bean.on(canvasElement, 'mouseleave', function (e) {
        if (moved) {
          AppInput.drag(e.offsetX, e.offsetY, 2);
          moved = false;
          mdown = false;
        } else {
            AppInput.leave(e.offsetX, e.offsetY);
        }
    });
  
    window.bean.on(canvasElement, 'contextmenu', function (e) {
        e.preventDefault();
        AppInput.contextmenu(e.offsetX, e.offsetY);
    });
}

export default mouseInput;
