import * as React from 'react';

var panel = {
    dragging: false,
    update: null,
    children: null,
    x: 0,
    y: 0,
}

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
      () => { stateRef.current = state },
      [state]
    )
    return [state, stateRef, setState];
}

const DragPanel = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);

    React.useEffect(() => {
        panel.update = () => {
            setCounter(counterRef.current + 1);
        }
        return () => {
            panel.update = null;
        };
    }, []);

    if (!panel.dragging) {
        return null;
    }

    var style = {top: panel.y, left: panel.x, position: 'absolute'};

    return <div className="dragPanel"><div style={style}>{panel.children}</div></div>
}

export {panel, DragPanel}