import React from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Config from '../common/Config'
import LekhDraggable from '../draggable/LekhDraggable';
import shapes from './Shapes'
import LibCell from '../common/LibCell';
import {
    AiOutlineCaretDown,
    AiOutlineCaretUp,
  } from "react-icons/ai"

const MiniLib = (props) => {
    shapes.search('');
    const getShapeIcons = () => {
        return shapes.getMiniGroup().shapes.map((shape, index) => {
            return (
                <LekhDraggable id={index} key={index} onStop={(e, data) => {props.onDragStop(e, data, shape.category, shape.num, popperRef)}}>
                    <LibCell category={shape.category} num={shape.num} id={index} size={45}/>
                </LekhDraggable>
            )
        })
    }

    var {anchorEl, placement} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }
    var popperRef = React.createRef();
    return (
        <div>
            <Popper ref={popperRef} open={true} anchorEl={anchorEl} placement={placement} transition>
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="popper_paper poper_paper_res">
                            {window.matchMedia(Config.mediaQuery).matches && (
                            <div className="shape-count">
                                <AiOutlineCaretUp onClick={() => props.expand(true)}/>
                            </div>)}
                            {getShapeIcons()}
                            {!window.matchMedia(Config.mediaQuery).matches && (
                            <div className="shape-count">
                                <AiOutlineCaretDown onClick={() => props.expand(true)}/>
                            </div>)}
                         </Paper>
                     </Fade>
                )}
            </Popper>
        </div>
    )
}

export default MiniLib;
