import './ShapeLib.css'
import React from 'react'
import canvas from '../common/Canvas'
import Config from '../common/Config'
import util from '../common/Util'
import MiniLib from './MiniLib';
import FullLib from './FullLib';
import shapes from './Shapes'

const ShapeLibrary = (props) => {
    const [expanded, setExpanded] = React.useState(shapes.expanded);

    const onDragStop = (event, data, categ, num, containerRef) => {
        var loc = util.mouseLocation(event);
        if (loc == null) {
            return;
        }
        var x = loc.x;
        var y = loc.y;
        
        var popUpRect = containerRef.current.getBoundingClientRect()
        if (y>= popUpRect.top && y<= popUpRect.bottom && x >= popUpRect.left && x <= popUpRect.right) {
            var mid = canvas.getMidPoint();
            x = mid.x;
            y = mid.y;
        }
        x = canvas.maprx(x);
        y = canvas.mapry(y);
        window.Module.libraryStore().addObjectToCanvas(categ, num, x, y);
        canvas.draw();
    }

    const onExpand = (val) => {
        setExpanded(val);
        shapes.expanded = val;
    }

    var {anchorEl, placement} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }

    if (expanded) {
        return <FullLib anchorEl={anchorEl} placement={placement} onDragStop={onDragStop} expand={onExpand} />
    }
    return (
        <MiniLib anchorEl={anchorEl} placement={placement} onDragStop={onDragStop} expand={onExpand}/>
    )
}

export default ShapeLibrary;
