import stencils from "./Stencils"
import libnames from '../data/libnames.json'

var shapes = {
    expanded: false,
    libraryColCount: 4,
    scroll: 0,
    searchText: ''
}

const loadingUrls = new Set()

var miniGroup = {
    name: 'Default',
    shapes: [
        {category: 0, num: 0, name: ''},
        {category: 0, num: 2, name: ''},
        {category: 0, num: 3, name: ''},
        {category: 0, num: 5, name: ''},
        {category: 0, num: 6, name: ''},
        {category: 0, num: 7, name: ''},
        {category: 0, num: 8, name: ''},
        {category: 2, num: 1, name: ''},
        {category: 2, num: 2, name: ''},
        {category: 2, num: 5, name: ''},
        {category: 2, num: 6, name: ''},
        {category: 2, num: 10, name: ''},
        {category: 2, num: 20, name: ''},
        {category: 3, num: 0, name: ''},
        {category: 3, num: 4, name: ''},
        {category: 5, num: 0, name: ''},
        {category: 5, num: 4, name: ''},
        {category: 5, num: 5, name: ''},
        {category: 5, num: 6, name: ''},
        {category: 5, num: 7, name: ''},
        {category: 5, num: 8, name: ''},
        {category: 5, num: 9, name: ''},
        {category: 5, num: 10, name: ''},
        {category: 4, num: 1, name: ''}
    ]
}

function initEmptyCategs() {
    for (let item of stencils) {
        window.Module.libraryStore().initEmptyCateg(item[0], item[1], item[2], item[3], item[4]);
    }
}

async function loadTemplate(name) {
    await fetch(name)
      .then(response => response.json())
      .then(response => {
        window.Module.libraryStore().initObjects(JSON.stringify(response));
      })
}
  
async function loadAll() {
    window.Module.libraryStore().initNames(JSON.stringify(libnames));
    await loadTemplate("templates/v4/templates.json")
    await loadTemplate("templates/v4/connection_arrows.json")
    await loadTemplate("templates/v4/basic.json")
    await loadTemplate("templates/v4/container.json")
    await loadTemplate("templates/v4/flowchart.json")
    await loadTemplate("templates/v4/arrow.json")
    await loadTemplate("templates/v4/callout.json")
    await loadTemplate("templates/v4/misc.json")
}

shapes.initialize = async function() {
    await loadAll();  
    window.Module.libraryStore().init(shapes.libraryColCount);
    initEmptyCategs();
    window.Module.libraryStore().onDisplaySettingChange();
}

shapes.groupCount = function() {
    return window.Module.getShapeLibGroupCount();
}

shapes.getGroup = function(idx) {
    var group = window.Module.getShapeLibGroup(idx);
    return group;
}

shapes.expandGroup = function(idx, flag) {
    window.Module.expandShapeLibGroup(idx, flag);
}

shapes.getShapes = function(idx, cb) {
    var group = window.Module.getShapeLibGroup(idx);
    if (group == null) {
        return;
    }
    if (group.contentUrl) {
        if (loadingUrls.has(group.contentUrl)) {
            return;
        }
        loadingUrls.add(group.contentUrl);
        fetch(group.contentUrl)
            .then(response => response.json())
            .then(response => {
                window.Module.libraryStore().initObjects(JSON.stringify(response));
                cb();
                loadingUrls.delete(group.contentUrl);
            });
    }
}

shapes.getMiniGroup = function() {
    return miniGroup;
}

shapes.getDisplaySettings = function() {
    return window.Module.getShapeLibDisplaySettings();
}

shapes.setDisplaySetting = function(name, flag) {
    window.Module.libraryStore().addDisplaySetting(name, flag);
    window.Module.libraryStore().onDisplaySettingChange();
}

shapes.search = function(text) {
    shapes.searchText = text;
    window.Module.libraryStore().filterObjects(text);
}

export default shapes
