import React from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Config from './Config'

const TextModePopup = (props) => {
    
    var {open, anchorEl, placement} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }
    return (
        <div>
            <Popper open={open} anchorEl={anchorEl} placement={placement} transition>
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="popper_paper poper_paper_res">
                            <span>Text can also be added by double clicking on the board</span>
                         </Paper>
                     </Fade>
                )}
            </Popper>
        </div>
    )
}

export default TextModePopup;
