import AppInput from './AppInput'
import canvas from './Canvas'
import menuState from './MenuState';

var touchInput = {}

const panThreshold = 6;
const doublePanThreshold = 12;
const zoomThreshold = 25;
const doubleTapIntervalMS = 300;

var tap = {
    active: false,
    discarded: false,
    touch: null,
    timer: null
}

var twoFingerTap = {
    active: false,
    discarded: false,
    touches: [],
    firstRemoved: false,
    secondRemoved: false,
    lastPos: [{x:0, y:0}, {x:0, y:0}, {x:0, y:0}, {x:0, y:0}]
}

var pan = {
    active: false,
    discarded: false,
    touch: null,
    lastTouch: null,
    buffer: []
}

var doublePan = {
    active: false,
    discarded: false,
    touches: [],
    lastPos: [{x:0, y:0}, {x:0, y:0}, {x:0, y:0}, {x:0, y:0}]
}

var zoom = {
    active: false,
    discarded: false,
    touches: [],
    lastPos: [{x:0, y:0}, {x:0, y:0}, {x:0, y:0}, {x:0, y:0}],
    origZoom: 1.0
}

var gestures = [tap, twoFingerTap, pan, doublePan, zoom];
var activeGesture = null;

var activeTouches = [];

function initPos(gest) {
    gest.lastPos[0].x = gest.touches[0].x;
    gest.lastPos[0].y = gest.touches[0].y;

    gest.lastPos[1].x = gest.touches[1].x;
    gest.lastPos[1].y = gest.touches[1].y;

    gest.lastPos[2].x = gest.touches[0].x;
    gest.lastPos[2].y = gest.touches[0].y;

    gest.lastPos[3].x = gest.touches[1].x;
    gest.lastPos[3].y = gest.touches[1].y;
}

function hasZoomCond(gest) {
    var d1 = dist(gest.lastPos[0], gest.lastPos[1]);
    var d2 = dist(gest.lastPos[2], gest.lastPos[3]);
    return Math.abs(d1 - d2) > zoomThreshold;
}

function hasPanCond(gest) {
    if (dist(gest.lastPos[0], gest.lastPos[2]) > doublePanThreshold &&
        dist(gest.lastPos[1], gest.lastPos[3]) > doublePanThreshold) {
        var mid1 = {x: (gest.lastPos[0].x + gest.lastPos[1].x) / 2, y: (gest.lastPos[0].y + gest.lastPos[1].y) / 2}
        var mid2 = {x: (gest.lastPos[2].x + gest.lastPos[3].x) / 2, y: (gest.lastPos[2].y + gest.lastPos[3].y) / 2}
        return dist(mid1, mid2) > doublePanThreshold;
    }
    return false;
}

function findTouches(gest, touches) {
    var t1 = null;
    var t2 = null;

    for (var i = 0; i < touches.length; i++) {
        if (gest.touches.length > 0 && touches[i].id == gest.touches[0].id) {
            t1 = touches[i];
        }
        if (gest.touches.length > 1 && touches[i].id == gest.touches[1].id) {
            t2 = touches[i];
        }
    }

    return {t1: t1, t2: t2};
}

function updatePos(gest, t1, t2, shift) {
    if (shift) {
        gest.lastPos[0].x = gest.lastPos[2].x;
        gest.lastPos[0].y = gest.lastPos[2].y;

        gest.lastPos[1].x = gest.lastPos[3].x;
        gest.lastPos[1].y = gest.lastPos[3].y;
    }
    if (t1 == null) {
        gest.lastPos[2].x = gest.lastPos[0].x;
        gest.lastPos[2].y = gest.lastPos[0].y;
    } else {
        gest.lastPos[2].x = t1.x;
        gest.lastPos[2].y = t1.y;
    }
    if (t2 == null) {
        gest.lastPos[3].x = gest.lastPos[1].y;
        gest.lastPos[3].y = gest.lastPos[1].y;
    } else {
        gest.lastPos[3].x = t2.x;
        gest.lastPos[3].y = t2.y;
    }
}

function mapTouches(touches) {
    var ret = []
    for (var i = 0; i < touches.length; i++) {
        ret.push({
            x: touches[i].pageX,
            y: touches[i].pageY,
            id: touches[i].identifier
        });
    }
    return ret;
}

function dist(t1, t2) {
    var dx = t1.x - t2.x;
    var dy = t1.y - t2.y;
    return Math.sqrt(dx*dx + dy*dy);
}

touchInput.attachEvents = function(canvasElement) {
    canvasElement.addEventListener('touchstart', function(ev) {
      ev.preventDefault();
      touchInput.start(ev.changedTouches);
    });
    canvasElement.addEventListener('touchmove', function(ev) {
      ev.preventDefault();
      touchInput.move(ev.targetTouches);
    });
    canvasElement.addEventListener('touchend', function(ev) {
      ev.preventDefault();
      touchInput.end(ev.changedTouches);
    });
    canvasElement.addEventListener('touchleave', function(ev) {
      ev.preventDefault();
      touchInput.cancel(ev.changedTouches);
    });
    canvasElement.addEventListener('touchcancel', function(ev) {
      ev.preventDefault();
      touchInput.cancel(ev.changedTouches);
    });
  }

touchInput.inProgress = function() {
    return activeTouches.length > 0;
}

touchInput.start = function(touches) {
    if (activeGesture != null) {
        return;
    }
    touches = mapTouches(touches);

    for (var i = 0; i < touches.length; i++) {
        activeTouches.push(touches[i]);
    }
    for (var i = 0; i < gestures.length; i++) {
        if (!gestures[i].discarded) {
            gestures[i].start(touches);
            if (gestures[i].active) {
                activeGesture = gestures[i];
                break;
            }
        }
    }
}

touchInput.move = function(touches) {
    touches = mapTouches(touches);
    if (activeGesture != null) {
        activeGesture.move(touches);
    } else {
        for (var i = 0; i < gestures.length; i++) {
            if (!gestures[i].discarded) {
                gestures[i].move(touches);
                if (gestures[i].active) {
                    activeGesture = gestures[i];
                    break;
                }
            }
        }
    }
}

touchInput.end = function(touches) {
    touches = mapTouches(touches);
    for (var i = 0; i < touches.length; i++) {
        for (var j = 0; j < activeTouches.length; j++) {
            if (touches[i].id == activeTouches[j].id) {
                activeTouches.splice(j, 1);
                break;
            }
        }
    }
    if (activeGesture != null) {
        activeGesture.end(touches);
    } else {
        for (var i = 0; i < gestures.length; i++) {
            if (!gestures[i].discarded) {
                gestures[i].end(touches);
                if (gestures[i].active) {
                    activeGesture = gestures[i];
                    break;
                }
            }
        }
    }
    if (activeTouches.length == 0) {
        touchInput.reset();
    }
}

touchInput.cancel = function(touches) {
    touchInput.reset();
}

touchInput.reset = function() {
    for (var i = 0; i < gestures.length; i++) {
        gestures[i].reset();
        gestures[i].discarded = false;
        gestures[i].active = false;
    }
    activeGesture = null;
    activeTouches = [];
}

tap.start = function(touches) {
    if (tap.touch != null) {
        tap.discarded = true;
        return;
    }
    if (touches.length > 1) {
        tap.discarded = true;
        return;
    }
    tap.touch = touches[0];
}

tap.move = function(touches) {
    if (tap.touch == null) {
        return;
    }
    if (touches.length != 1 || touches[0].id != tap.touch.id) {
        tap.discarded = true;
    } else {
        if (dist(tap.touch, touches[0]) > panThreshold) {
            tap.discarded = true;
        }
    }
}

tap.end = function(touches) {
    if (tap.touch == null) {
        return;
    }
    if (touches.length != 1 || touches[0].id != tap.touch.id) {
        tap.discarded = true;
    } else if (dist(tap.touch, touches[0]) > panThreshold) {
        tap.discarded = true;
    } else {
        var x = tap.touch.x;
        var y = tap.touch.y;
        if (tap.timer != null) {
            clearTimeout(tap.timer);
            tap.timer = null;
            AppInput.dbclick(x, y);
        } else {
            tap.timer = setTimeout(function() {
                tap.timer = null;
                AppInput.click(x, y);
            }, doubleTapIntervalMS);
        }
    }
    tap.active = false;
}

tap.reset = function() {
    tap.touch = null;
    tap.active = false;
    if (activeGesture == tap) {
        activeGesture = null;
    }
}

twoFingerTap.start = function(touches) {
    if (twoFingerTap.touches.length + touches.length > 2) {
        twoFingerTap.discarded = true;
        return;
    }

    for (var i = 0; i < touches.length; i++) {
        twoFingerTap.touches.push(touches[i]);
    }

    if (twoFingerTap.touches.length == 2) {
        initPos(twoFingerTap);
        twoFingerTap.firstRemoved = false;
        twoFingerTap.secondRemoved = false;
    }
}

twoFingerTap.move = function(touches) {
    if (twoFingerTap.touches.length != 2) {
        return;
    }

    var ts = findTouches(twoFingerTap, touches);
    if (ts.t1 == null && ts.t2 == null) {
        return;
    }

    updatePos(twoFingerTap, ts.t1, ts.t2, false);
}

twoFingerTap.end = function(touches) {
    var ts = findTouches(twoFingerTap, touches);
    if (ts.t1 != null) {
        twoFingerTap.firstRemoved = true;
    }
    if (ts.t2 != null) {
        twoFingerTap.secondRemoved = true;
    }
    if (twoFingerTap.firstRemoved && twoFingerTap.secondRemoved) {
        if (dist(twoFingerTap.lastPos[0], twoFingerTap.lastPos[2]) < panThreshold &&
        dist(twoFingerTap.lastPos[1], twoFingerTap.lastPos[3]) < panThreshold) {
            var x = (twoFingerTap.lastPos[0].x + twoFingerTap.lastPos[1].x) / 2;
            var y = (twoFingerTap.lastPos[0].y + twoFingerTap.lastPos[1].y) / 2;
            AppInput.contextmenu(x, y);
        }
        twoFingerTap.reset();
    }
}

twoFingerTap.reset = function() {
    twoFingerTap.touches = [];
    twoFingerTap.active = false;
    twoFingerTap.firstRemoved = false;
    twoFingerTap.secondRemoved = false;
}

pan.start = function(touches) {
    if (pan.active) {
        return;
    }

    if (pan.touch != null) {
        pan.discarded = true;
        return;
    }
    if (touches.length > 1) {
        pan.discarded = true;
        return;
    }
    pan.touch = touches[0];
    pan.lastTouch = pan.touch;
    pan.buffer.push(touches[0]);
}

pan.move = function(touches) {
    if (pan.touch == null || touches.length > 1 ||
        touches[0].id != pan.touch.id) {
        return;
    }
    var touch = touches[0];
    pan.lastTouch = touch;

    if (pan.active) {
        AppInput.drag(touch.x, touch.y, 1);
    } else if (dist(touch, pan.touch) > panThreshold) {
        pan.active = true;
        if (pan.buffer.length > 0 && menuState.inputMode == menuState.modes.smartDraw) {
            // for intelligent mode, ignore the first touch point
            pan.buffer.splice(0, 1);
        }
        if (pan.buffer.length > 0) {
            AppInput.drag(pan.buffer[0].x, pan.buffer[0].y, 0);    
        } else {
            AppInput.drag(touch.x, touch.y, 0);
        }
        for (var i = 1; i < pan.buffer.length; i++) {
            AppInput.drag(pan.buffer[i].x, pan.buffer[i].y, 1);    
        }
        AppInput.drag(touch.x, touch.y, 1);
        pan.buffer = [];
    } else {
        pan.buffer.push(touch);
    }
}

pan.end = function(touches) {
    pan.reset();
}

pan.reset = function() {
    if (pan.active) {
        AppInput.drag(pan.lastTouch.x, pan.lastTouch.y, 2);
    }
    pan.active = false;
    pan.touch = null;
    pan.lastTouch = null;
    pan.buffer = [];
    if (activeGesture == pan) {
        activeGesture = null;
    }
}

doublePan.start = function(touches) {
    if (doublePan.active) {
        return;
    }

    if (doublePan.touches.length + touches.length > 2) {
        doublePan.discarded = true;
        return;
    }

    for (var i = 0; i < touches.length; i++) {
        doublePan.touches.push(touches[i]);
    }

    if (doublePan.touches.length == 2) {
        initPos(doublePan);
    }
}

doublePan.move = function(touches) {
    if (doublePan.touches.length != 2) {
        return;
    }

    var ts = findTouches(doublePan, touches);
    if (ts.t1 == null && ts.t2 == null) {
        return;
    }

    if (doublePan.active) {
        updatePos(doublePan, ts.t1, ts.t2, true);
        var mid1x = (doublePan.lastPos[0].x + doublePan.lastPos[1].x) / 2;
        var mid1y = (doublePan.lastPos[0].y + doublePan.lastPos[1].y) / 2;
        var mid2x = (doublePan.lastPos[2].x + doublePan.lastPos[3].x) / 2;
        var mid2y = (doublePan.lastPos[2].y + doublePan.lastPos[3].y) / 2;
        canvas.moveDelta(mid2x - mid1x, mid2y - mid1y);
    } else {
        updatePos(doublePan, ts.t1, ts.t2, false);
        if (hasPanCond(doublePan) && !hasZoomCond(doublePan)) {
            doublePan.active = true;  
        }
    }
}

doublePan.end = function(touches) {
    doublePan.reset();
}

doublePan.reset = function() {
    doublePan.touches = [];
    doublePan.active = false;
    if (activeGesture == doublePan) {
        activeGesture = null;
    }
}

zoom.start = function(touches) {
    if (zoom.active) {
        return;
    }

    if (zoom.touches.length + touches.length > 2) {
        zoom.discarded = true;
        return;
    }

    for (var i = 0; i < touches.length; i++) {
        zoom.touches.push(touches[i]);
    }

    if (zoom.touches.length == 2) {
        initPos(zoom);
    }
}

zoom.move = function(touches) {
    if (zoom.touches.length != 2) {
        return;
    }

    var ts = findTouches(zoom, touches);
    if (ts.t1 == null && ts.t2 == null) {
        return;
    }

    updatePos(zoom, ts.t1, ts.t2, false);
    if (zoom.active) {
        var d1 = dist(zoom.lastPos[0], zoom.lastPos[1]);
        var d2 = dist(zoom.lastPos[2], zoom.lastPos[3]);
        canvas.zoom((zoom.origZoom * d2) / d1);
    } else {
        if (hasZoomCond(zoom)) {
            zoom.origZoom = window.Module.getZoom()
            zoom.active = true;
        }
    }
}

zoom.end = function(touches) {
    zoom.reset();
}

zoom.reset = function() {
    zoom.touches = [];
    zoom.active = false;
    zoom.origZoom = 1.0;
    if (activeGesture == zoom) {
        activeGesture = null;
    }
}

export default touchInput;