import React, { useState } from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Style from './SideBarMenu/SideBarStyle';
import { makeStyles } from "@material-ui/core/styles";
import { AiOutlineMinus, AiOutlineDash, AiOutlineSmallDash } from "react-icons/ai";
import { RiCheckboxBlankCircleFill } from "react-icons/ri";
import drawStyle from './DrawStyle'
import ColorList from './ColorList'
import Config from './Config'

const useStyles = makeStyles(Style);

const StrokeStyle = (props) => {
    const classes = useStyles();

    const [colorIndex, setColorIndex] = useState(drawStyle.colorIndex)
    const [dashIndex, setDashIndex] = useState(drawStyle.dashIndex)
    const [widthIndex, setWidthIndex] = useState(drawStyle.widthIndex)

    const widthCircles = [
        {icon: <RiCheckboxBlankCircleFill size="1em" id="0" />},
        {icon: <RiCheckboxBlankCircleFill size="1.2em" id="1" />},
        {icon: <RiCheckboxBlankCircleFill size="1.4em" id="2" />},
        {icon: <RiCheckboxBlankCircleFill size="1.6em" id="3" />},
        {icon: <RiCheckboxBlankCircleFill size="1.8em" id="4" />},
    ]

    const dashLines = [
        {icon: <AiOutlineMinus size="2.2em" id="0" />},
        {icon:<AiOutlineSmallDash size="2em" style={{marginBottom:'3px'}}  id="1"  />},
        {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}   id="2" />},
        {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}  id="3" />},
    ]
    
    const onColorChange = (index, color) =>{
        setColorIndex(index)
        drawStyle.setColor(index,color);
    }

    const onDashChange = (index, iconsId) => {
        setDashIndex(iconsId)
        drawStyle.setDashIndex(index);
    }

    const onWidthChange = (index, iconsId) => {
        setWidthIndex(iconsId)
        drawStyle.setWidthIndex(index);
    }

    const getDashLines = () => {
                      return ( 
                          <div className={classes.spaceBwtRound}>
                        {
                           dashLines.map((dashLine, index) =>{
                                return(
                                    <span key={index} className={`${dashLine.icon.props.id == dashIndex ? "changeBorder" : ""} `} 
                                        onClick={() => onDashChange(index, dashLine.icon.props.id) }>
                                        {dashLine.icon}
                                    </span>
                                )
                            })
                        }
                        </div>
                      )
    }

    const getWidthCircles = () => {
        return ( 
            <div className={classes.spaceBwt} >
          {
             widthCircles.map((wcircle, index) =>{
                  return(
                      <span key={index} className={`${wcircle.icon.props.id == widthIndex ? "changeBorderRound" : "defaultBorder"} `} 
                        onClick={() => onWidthChange(index, wcircle.icon.props.id) }>
                          {wcircle.icon}
                      </span>
                  )
              })
          }
          </div>
        )
    }
    var {open, anchorEl, placement, subMenuIcons} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }

    return (
        <div>
            <Popper open={open} anchorEl={anchorEl} placement={placement} transition
            >
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="smart_draw_paper smart_draw_res">
                            {getWidthCircles()}
                            {getDashLines()}
                            <ColorList colors={subMenuIcons} selectedIndex={colorIndex} 
                                onColorChange={onColorChange}/>
                         </Paper>
                    </Fade>
                )}
            </Popper>
        </div>
    )
}

export default StrokeStyle;
