import React, { useState } from "react"
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Config from '../common/Config'
import shapes from './Shapes'
import ShapesGroup from './ShapesGroup';
import Dialog from "@material-ui/core/Dialog"
import Switch from "@material-ui/core/Switch"
import keyboard from '../common/KeyboardInput'

import {
    AiOutlineCaretDown,
    AiOutlineCaretUp,
    AiOutlineCaretRight,
    AiTwotoneSetting,
    AiOutlineClose
} from "react-icons/ai"

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

var searchTimer = null;

const SearchBox = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = function () {
        setCounter(counterRef.current + 1);
    }
    const handleSearch = (e) => {
        var val = e.target.value;
        if (!val) {
            val = '';
        }
        shapes.searchText = val;
        if (searchTimer == null) {
            searchTimer = window.setTimeout(function() {
                shapes.search(shapes.searchText);
                reload();
                searchTimer = null;
                props.searchCompleted();
            }, 500);
        }
        reload();
    }
    return <input placeholder="Search"
        id="shapeLibSearchBox"
        value={shapes.searchText}
        onChange={handleSearch}
    />
}

const FullLib = (props) => {
    const [showMoreSettings, showMoreSettingsRef, setShowMoreSttings] = useRefState(false)
    const [counter, counterRef, setCounter] = useRefState(0);

    var { anchorEl, placement } = props;

    const handleKeyDown = (event) => {
        var textBox = document.getElementById("shapeLibSearchBox");
        if (document.activeElement == textBox) {
            return true;
        }
        if (showMoreSettingsRef.current) {
            let key = event.key.toLowerCase();
            if (key == 'escape') {
                setShowMoreSttings(false);
            }
            return true;
        }
        return false;
    }

    React.useEffect(() => {
        keyboard.addListener(handleKeyDown);
        window.setTimeout(function () {
            if (shapesGroupRef.current) {
                shapesGroupRef.current.scrollTop = shapes.scroll;
                shapesGroupRef.current.addEventListener('scroll', (event) => {
                    if (shapesGroupRef.current) {
                        shapes.scroll = shapesGroupRef.current.scrollTop;
                    }
                });
            }
        }, 5);
        return () => {
            keyboard.removeListener(handleKeyDown);
        }
    }, [])

    const reload = function () {
        setCounter(counterRef.current + 1);
    }

    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }
    var popperRef = React.createRef();
    var shapesGroupRef = React.createRef();

    const onDragStop = (e, data, categ, num) => {
        props.onDragStop(e, data, categ, num, popperRef);
    }

    var icons = [];
    for (let i = 0; i < shapes.groupCount(); i++) {
        icons.push(<ShapesGroup idx={i} onDragStop={onDragStop} />);
    }
    var displaySettings = [];
    if (showMoreSettings) {
        displaySettings = shapes.getDisplaySettings();
    }

    return (
        <div>
            <Popper ref={popperRef} open={true} anchorEl={anchorEl} placement={placement} transition>
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={0}>
                        <Paper className="popper_paper poper_paper_res">
                            <div className="shapes-settings">
                                <SearchBox searchCompleted={reload} />
                                <AiTwotoneSetting
                                    className="shapes-settings-icon"
                                    onClick={() => setShowMoreSttings(true)}
                                />
                                <AiOutlineClose className="shapes-settings-close" onClick={() => props.expand(false)}/>
                            </div>
                            <div ref={shapesGroupRef} className="shapes-group-div">
                                {icons}
                            </div>
                            {!window.matchMedia(Config.mediaQuery).matches && (
                                <div className="shape-count">
                                    <AiOutlineCaretUp onClick={() => props.expand(false)} />
                                </div>
                            )}
                        </Paper>
                    </Fade>
                )}
            </Popper>
            {showMoreSettings && (
                <Dialog open={showMoreSettings}>
                    <div className="show-more-settings">
                        <div className="show-more-settings-title">
                            <h2>Manage Shapes</h2>

                            <AiOutlineClose
                                onClick={() => setShowMoreSttings(false)}
                                className="show-more-close"
                            />
                        </div>

                        <div className="show-more-settings-inner">
                            {displaySettings.map(c => (
                                <div>
                                    <h4>{c.name}</h4>
                                    <Switch color="primary" checked={c.enabled} onChange={(event) => { shapes.setDisplaySetting(c.name, event.target.checked); reload(); }} />
                                </div>
                            ))}
                        </div>
                    </div>
                </Dialog>
            )}
        </div>
    )
}

export default FullLib;
