import canvas from './Canvas';
import command from './Command'
import AppInput from './AppInput'

var keyboardInput = { 
    showingDialog: false
};

var listeners = [];
var postListeners = [];

const isMac = navigator.userAgent.indexOf("OS X") > 0;

var handlers = {
    'a': command.selectAll,
    'c': command.copy,
    'x': command.cut,
    'v': command.paste,
    'b': command.addBox,
    'e': command.addCircle,
    'u': command.addUser,
    'z': command.undo,
}

keyboardInput.addListener = function(cb) {
    listeners.push(cb);
}

keyboardInput.removeListener = function(cb) {
    for (var i = 0; i < listeners.length; i++) {
        if (listeners[i] == cb) {
            listeners.splice(i, 1);
            return;
        }
    }
}

keyboardInput.addPostListener = function(cb) {
    postListeners.push(cb);
}

keyboardInput.removePostListener = function(cb) {
    for (var i = 0; i < postListeners.length; i++) {
        if (postListeners[i] == cb) {
            postListeners.splice(i, 1);
            return;
        }
    }
}

keyboardInput.handleKeyDown = function(event) {
    if (AppInput.isEditingText() || keyboardInput.showingDialog) {
        // do not handle keyboard while showing a dialog or editing shape text
        return;
    }

    for (let cb of listeners) {
        if (cb(event)) {
            return;
        }
    }

    let key = event.key.toLowerCase();
    var handled = false;
    if (key == 'backspace' || key == 'delete') {
        command.del();
        handled = true;
    } else if (key == 'escape') {
        command.escape();
        handled = true;
    } else {
        var handler = handlers[key];
        if (!event.shiftKey && !event.altKey) {
            if (handler && ((isMac && event.metaKey) || (!isMac && event.ctrlKey))) {
                handler();
                handled = true;
            }
        }
        if (isMac && key == 'z' && event.shiftKey && event.metaKey) {
            command.redo();
            handled = true;
        }
        if (!isMac && key == 'z' && event.shiftKey && event.ctrlKey) {
            command.redo();
            handled = true;
        }
        if (!isMac && key == 'y' && event.ctrlKey && !event.shiftKey && !event.altKey) {
            command.redo();
            handled = true;
        }
    }
    if (handled) {
        event.preventDefault();
        canvas.draw();
    }

    for (let cb of postListeners) {
        cb(event);
    }
}

keyboardInput.attachEvents = function(canvasElement) {
    document.addEventListener("keydown", function(event) {
        keyboardInput.handleKeyDown(event);
    });
}

export default keyboardInput;
