import './ShapeProps.css'

import React from "react"
import  Help  from "./Help"
import  Property  from "./Property"
import AppInput from "../common/AppInput"
import keyboardInput from '../common/KeyboardInput'

var shapePropsUtil = { }

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const ShapeProps = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    const handleKey = (event) => {
        let key = event.key.toLowerCase();
        if (key == 'escape') {
            if (shapePropsUtil.isPropertyWindowOpen()) {
                shapePropsUtil.dismissWindow();
                event.preventDefault();
                reload();
                return true
            }
        }
        return false;
    }

    React.useEffect(() => {
        keyboardInput.addListener(handleKey);
        AppInput.showShapeProps = () => {reload()}
        return () => {
            keyboardInput.removeListener(handleKey);
            AppInput.showShapeProps = () => {}
        }
      }, [])

    if (!window.Module.inputReceiver) {
        return null;
    }
    var propsEdit = false;
    var showHelp = false;
    if (window.Module.inputReceiver().needPropertyEditing()) {
        propsEdit = true;
    } else if (window.Module.inputReceiver().needToShowShapeHelp()) {
        showHelp = true;
    }

    if (propsEdit == false && showHelp == false) {
        return null;
    }

    if (propsEdit) {
        return <Property reload={reload} />;
    } else if (showHelp) {
        return <Help reload={reload} />;
    }
    return null;
}

shapePropsUtil.isPropertyWindowOpen = function() {
    return window.Module.inputReceiver().needPropertyEditing() ||
        window.Module.inputReceiver().needToShowShapeHelp();
}

shapePropsUtil.dismissWindow = function() {
    window.Module.inputReceiver().setNeedPropertyEditing(false);
    window.Module.inputReceiver().setNeedToShowShapeHelp(false);
}

export {shapePropsUtil, ShapeProps};