import React from "react"
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import {AiOutlineCheck, AiOutlineLeft} from "react-icons/ai";
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';

import canvas from '../common/Canvas'

const EnumPicker = (props) => {
    const [selectedVal, setSelectedVal] = React.useState('')
    var properties = props.editor.getProperties();
    var idx = props.idx;
    var name = properties[idx].name;
    var value = properties[idx].value;
    var items = properties[idx].editor.enumItems;

    const selectItem = (item) => {
        props.editor.setVal(idx, item);
        setSelectedVal(item);
        canvas.draw();
    }

    return (
        <div className="enum_picker">
            <div className="enum_picker_header">
                <IconButton onClick={props.done}> 
                    <AiOutlineLeft size={18}/>
                </IconButton>
                <div>{name}</div>
            </div>
            <List component="nav" aria-label="contacts">
                {items.map((item, index) => {
                    return (
                        <ListItem key={item} button onClick={()=>selectItem(item)}>
                            <ListItemText primary={item} />
                            {item == value &&
                                <ListItemSecondaryAction>
                                    <AiOutlineCheck />
                                </ListItemSecondaryAction>}
                        </ListItem>)
                })}
            </List>
        </div>
    );
}

export default EnumPicker;
