import React from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import NestedMenuItem from "material-ui-nested-menu-item";
import ListItemText from '@material-ui/core/ListItemText';
import canvas from './Canvas'
import command from './Command'
import ArrowRight from '@material-ui/icons/ArrowRight'

const shapesMenus = [
    {id:'box', name: 'Box', key1:'Ctrl + B', key2: ' B', disabled: false},
    {id:'ellipse', name: 'Circle', key1:' Ctrl + E', key2: ' E', disabled: false},
    /*{id:'table', name: 'Table', key1:'ctrl l', key2: ' L', disabled: false},*/
    {id:'user', name: 'User', key1:'Ctrl + U', key2: ' U', disabled: false},
    {id:'note', name: 'Note', key1:'', key2: '', disabled: false},
    {id:'db', name: 'Database', key1:'', key2: '', disabled: false},
    {id:'cloud', name: 'Cloud', key1:'', key2: '', disabled: false},
    /*{id:'more', name: 'More', key1:'', key2: '', disabled: false},*/
]

const arrangeMenus = [
  {id:'moveToFront', name: 'Move To Front', key1:'', key2: '', disabled: false},
  {id:'moveToBack', name: 'Move To Back', key1:' ', key2: '', disabled: false},
]

var menus = [
    {id:'add', name: 'Add Shapes', key1: '', key2: '', div: false, disabled: false, submenu: shapesMenus},
    {id:'arrange', name: 'Arrange', key1: '', key2: '', div: true, disabled: false, submenu: arrangeMenus},
    {id:'select', name: 'Select All', key1: 'Ctrl + A', key2: ' A', div: true, disabled: false},
    {id:'cut', name: 'Cut', key1: 'Ctrl + X', key2: ' X', div: false, disabled: false},
    {id:'copy', name: 'Copy', key1: 'Ctrl + C', key2: ' C', div: false, disabled: false},
    {id:'paste', name: 'Paste', key1: 'Ctrl + V', key2: ' V', div: false, disabled: false},
    {id:'del', name: 'Delete', key1: 'Del', key2: 'Delete', div: false, disabled: false},
    //{id:'line', name: 'Line Style', key1: '', key2: '', div: false, disabled: false},
    //{id:'fill', name: 'Fill Color', key1: '', key2: '', div: false, disabled: false},
    //{id:'font', name: 'Font', key1: '', key2: '', div: false, disabled: false},
]

export default function ContextMenu(props) {
  const handleClose = () => {
    props.showHandler(false);
  };

  var mappedPt = {y: canvas.mapry(props.pos.y), x: canvas.maprx(props.pos.x)};

  const handleMenu = (id) => {
      if (id == "copy") {
        command.copy();
      } else if (id == "paste") {
        command.pasteAt(mappedPt);
      } else if (id == "cut") {
        command.cut();
      } else if (id == "del") {
        command.del();
      } else if (id == "select") {
        command.selectAll();
      } else if (id == "box") {
        command.addBox({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "ellipse") {
        command.addCircle({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "table") {        
      } else if (id == "user") {
        command.addUser({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "db") {
        command.addDb({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "cloud") {
        command.addCloud({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "note") {
        command.addNote({x:mappedPt.x, y:mappedPt.y});
      } else if (id == "moveToFront") {
        command.moveToFront();
      } else if (id == "moveToBack") {
        command.moveToBack();
      }

      canvas.draw();
      props.showHandler(false);
  };

  var cmStatus = window.Module.getContextMenuStatus(mappedPt);
  menus[1].disabled = !cmStatus.canArrage;
  menus[2].disabled = !cmStatus.canSelectAll;
  menus[3].disabled = !cmStatus.canCut;
  menus[4].disabled = !cmStatus.canCopy;
  menus[5].disabled = !cmStatus.canPaste;
  menus[6].disabled = !cmStatus.canDelete;
  canvas.draw();

  var keyName = 'key1';
  if (navigator.userAgent.indexOf("OS X") > 0) {
      // This is MAC
      keyName = "key2";
  }
  return (
      <Menu
        keepMounted
        open={true}
        onClose={handleClose}
        anchorReference="anchorPosition"
        anchorPosition={{top: props.pos.y, left: props.pos.x}}
        PaperProps={{
            style: {
                border: '2px solid #d3d4d5'
            },
        }}>
      {menus?.map((option, index) => {
          var subMenus = option.submenu;
          var rightIcon =  <ArrowRight />;
          if (index == 1) {
            rightIcon =  <ArrowRight style={{marginLeft: '126px'}}/>;
            if (option.disabled) {
              subMenus = null;
            }
          }
          if (subMenus) {
            return (<NestedMenuItem
                label={option.name}
                style={{ left: '4px' }}
                parentMenuOpen={true}
                divider={option.div}
                rightIcon={rightIcon}
                className="subIcon"
                disabled={option.disabled}
                key={option.id}>
                {
                option.submenu.map((subOption, index) => (
                    <MenuItem onClick={()=>{handleMenu(subOption.id)}} key={subOption.id} divider={subOption.div} disabled={subOption.disabled}>
                        <ListItemText primary={subOption.name} />
                        <ListItemSecondaryAction className="shortcutKeys">{subOption[keyName]}</ListItemSecondaryAction>
                    </MenuItem>
                    
                ))}
            </NestedMenuItem>);
          }
          return (
            <MenuItem onClick={()=>{handleMenu(option.id)}} key={option.id} divider={option.div} disabled={option.disabled} style={{ left: '4px' }}>
                <ListItemText primary={option.name} />
                <ListItemSecondaryAction className="shortcutKeys">{option[keyName]}</ListItemSecondaryAction>
            </MenuItem>
          )
       })
       }
      </Menu>
  );
}

/*
  &#x2318;  &#8984;  the Command Key symbol
  &#x2325;  &#8997;  the Option Key symbol
  &#x21E7;  &#8679;  the Shift Key symbol
  &#x2303;  &#8963;  the Control Key symbol
  &#x238B;  &#9099;  the ESC Key symbol
  &#x21ea;  &#8682;  the Capslock symbol
  &#x23ce;  &#9166;  the Return symbol
  &#x232b;  &#9003;  the Delete / Backspace symbol
  &#x21E5;  &#8677;  the Tab Key symbol
*/

