import React from 'react'
import {DraggableCore} from 'react-draggable';
import {panel} from './Panel'
import util from '../common/Util'

var dx = 0;
var dy = 0;

const LekhDraggable = (props) => {
    const [enabled, setEnabled] = React.useState(true);

    const updatePanel = () => {
        if (panel.update) {
            panel.update();
        }
    }

    const onDragStart = (e) => {
        var canvas = document.getElementById(props.id);
        var rect = canvas.getBoundingClientRect();
        panel.x = rect.x;
        panel.y = rect.y;
       
        panel.dragging = true;
        panel.children = React.cloneElement(props.children, {enabled: true});

        var loc = util.mouseLocation(e);
        if (loc != null) {
            dx = rect.x - loc.x;
            dy = rect.y - loc.y;
        }

        updatePanel();
        setEnabled(false);
    }

    const onDrag = (e) => {
        var loc = util.mouseLocation(e);
        if (loc != null) {
            panel.x = loc.x + dx;
            panel.y = loc.y + dy;
        }
        updatePanel();
    }

    const onDragStop = (e) => {
        props.onStop(e);
        window.setTimeout(function() {
            panel.dragging = false;
            updatePanel();
            setEnabled(true);
        }, 10);
    }

    var children = React.cloneElement(props.children, {enabled: enabled});

    return (
        <DraggableCore onStart={onDragStart} onDrag={onDrag} onStop={onDragStop}>
            {children}
        </DraggableCore>
      );
}

export {LekhDraggable as default};
