import React, { useState } from "react"
import { AiOutlineCaretDown, AiOutlineCaretRight } from "react-icons/ai"
import LekhDraggable from '../draggable/LekhDraggable';
import shapes from './Shapes'
import LibCell from '../common/LibCell';

const useRefState = initialValue => {
  const [state, setState] = React.useState(initialValue)
  const stateRef = React.useRef(state)
  React.useEffect(
    () => { stateRef.current = state },
    [state]
  )
  return [state, stateRef, setState];
}

const ShapesGroup = ({idx, onDragStop}) => {
  var group = shapes.getGroup(idx);
  const [counter, counterRef, setCounter] = useRefState(0);

  if (group == null) {
    return <div></div>;
  }

  if (group.shapes.length == 0 && group.contentUrl && group.expanded) {
    shapes.getShapes(idx, function() {
      setCounter(counterRef.current + 1);
    });
  }
  const handleCaretClick = () => {
    shapes.expandGroup(idx, !group.expanded)
    setCounter(counterRef.current + 1);
  }

  const getShapeIcons = () => {  
    return group.shapes.map((shape, index) => {
      var canvasId = idx + '_' + index;
      return (
        <div className="libCell">
        <LekhDraggable id={canvasId} key={index} onStop={(e, data) => {onDragStop(e, data, shape.category, shape.num)}}>
          <LibCell category={shape.category} num={shape.num} id={canvasId} size={40}/>
        </LekhDraggable>
        <span>{shape.name}</span>
        </div>
      )
    })
  }

  return (
    <div className="">
      <div onClick={handleCaretClick} className="shapes-group-inner">
        <span className="caret">
          {!group.expanded && <AiOutlineCaretRight />}
          {group.expanded && <AiOutlineCaretDown />}
        </span>
        <h4 className="group-shapes-name">{group.name}</h4>
      </div>
      {group.shapes.length == 0 && group.expanded && <span className="libCellLoadingSpan">Loading..</span>}
      {group.shapes.length > 0 && group.expanded && <div className="group-shapes">{getShapeIcons()}</div>}
    </div>
  )
}

export default ShapesGroup
