import React from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import Switch from "@material-ui/core/Switch"
import { AiOutlinePlus, AiOutlineMinus, AiOutlineRight } from "react-icons/ai";
import IconButton from '@material-ui/core/IconButton';
import Slider from '@material-ui/core/Slider';
import EnumPicker from "./EnumPicker"
import canvas from '../common/Canvas'

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const Property = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    
    const [showingEnum, setShowingEnum] = React.useState(false);
    const [enumPropIdx, setEnumPropIdx] = React.useState(-1);

    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    if (!window.Module.inputReceiver().needPropertyEditing()) {
        props.reload();
        return null;
    }

    var editor = window.Module.startPropertyEditing();
    if (!editor.isValid()) {
        window.Module.inputReceiver().setNeedPropertyEditing(false);
        props.reload();
        return null;
    }

    var properties = editor.getProperties();
    var rect = editor.getBound();
    rect.y -= 10;
    rect.height += 20
    const getBoundingClientRect = () =>  {
        return {
            x: rect.x,
            y: rect.y,
            width:rect.width,
            height: rect.height,
            top: rect.y,
            left: rect.x,
            bottom: rect.y + rect.height,
            right: rect.x + rect.width
        }
    }

    var anchorEl = {
        clientWidth: rect.width,
        clientHeight: rect.height,
        getBoundingClientRect,
    }

    const handleClickAway = (event) => {
        event.preventDefault();
        window.Module.inputReceiver().setNeedPropertyEditing(false);
        editor.finalize();
        props.reload();
    }

    const handleEnumSelect = (idx) => {
        setEnumPropIdx(idx);
        setShowingEnum(true);
    }

    const changeBoolVal = (idx) => {
        editor.setVal(idx, !properties[idx].value);
        canvas.draw();
        reload();
    }

    const changeNumberVal = (idx, plus) => {
        var factor = plus? 1 : -1;
        var prop = properties[idx];
        if (prop.editor.type == "IntMinMax" || prop.editor.type == "FloatMinMaxStep") {
            var step = prop.editor.step;
            if (step <= 0) {
                step = 1;
            }
            var val = prop.value + (factor * step);
            if (val < prop.editor.minVal) {
                val = prop.editor.minVal;
            }
            if (val > prop.editor.maxVal) {
                val = prop.editor.maxVal;
            }
            editor.setVal(idx, val);
        } else if (prop.editor.type == "Default") {
            var val = prop.value + factor;
            editor.setVal(idx, val);
        }
        canvas.draw();
        reload();
    }

    const changeSliderVal = (idx, newVal) => {
        var prop = properties[idx];
        var min = prop.editor.minVal;
        var max = prop.editor.maxVal;
        var range = max - min;
        if (range > 0) {
            var propVal = (newVal * range) / 100 + min;
            editor.setVal(idx, propVal);
        }
        canvas.draw();
        reload();
    }

    const getSliderVal = (idx) => {
        var prop = properties[idx];
        var min = prop.editor.minVal;
        var max = prop.editor.maxVal;
        var val = prop.value;
        var range = max - min;
        if (range > 0) {
            return (100 * (val - min)) / range ;
        } else {
            return 0;
        }
    }

    var items = [];
    if (showingEnum) {
        items.push(<EnumPicker editor={editor} idx={enumPropIdx} done={()=>setShowingEnum(false)}></EnumPicker>);
    } else {
        items.push(<div key="header" className="shape_property_header">Properties</div>)
        for (var i = 0; i < properties.length; i++) {
            let prop = properties[i];
            if (prop.editor.type != "Hidden") {
                var name = (<div className="shape_property_col_1">
                    <span>{prop.name}</span>
                </div>);
                var val = null;
                if (prop.valueType == "bool") {
                    let idx = i;
                    val = (<div className="shape_property_col_2">
                        <Switch checked={prop.value} color="primary" onChange={()=>{changeBoolVal(idx)}}></Switch>
                    </div>)

                } else if (prop.valueType == "int" || 
                    (prop.valueType == "float" && prop.editor.type == "FloatMinMaxStep")) {
                    let idx = i;
                    val = (<div className="shape_property_col_2">
                        <IconButton aria-label="plus" onClick={()=>changeNumberVal(idx, true)}>
                            <AiOutlinePlus />
                        </IconButton>
                        <div className="shape_property_col_2_val">{prop.value}</div>
                        <IconButton aria-label="minus">
                            <AiOutlineMinus onClick={()=>changeNumberVal(idx, false)}/>
                        </IconButton>
                    </div>)
                } else if (prop.valueType == "string") {
                    if (prop.editor.type == "EnumString") {
                        let idx = i;
                        val = (<div className="shape_property_col_2">
                            <IconButton aria-label="choose" onClick={() => { handleEnumSelect(idx) }}>
                                <AiOutlineRight />
                            </IconButton>
                            <div className="shape_property_col_2_val">{prop.value}</div>
                        </div>)
                    }
                } else if (prop.valueType == "float" && prop.editor.type == "FloatMinMax") {
                    let idx = i;
                    var item = (<div key={idx} className="shape_property_row_slider">
                        <div className="shape_property_row">
                            {name}
                            <div className="shape_property_col_separator"></div>
                            <div className="shape_property_col_2">
                                <span>{prop.value.toFixed(2)}</span>
                            </div>
                        </div>
                        <Slider className="value_slider" value={getSliderVal(idx)} onChange={(event, val)=>changeSliderVal(idx, val)}></Slider>
                    </div>);
                    items.push(item);
                    name = null;
                }
                if (name != null) {
                    let idx = i;
                    items.push(
                        <div key={idx} className="shape_property_row">
                            {name}
                            <div className="shape_property_col_separator"></div>
                            {val}
                        </div>
                    )
                }
            }
        }
    }
    return (
        <ClickAwayListener onClickAway={handleClickAway}>
            <Popper open={true} anchorEl={anchorEl} placement={"top"}>
                <div className="shape_property_paper">
                    {items}
                </div>
            </Popper>
        </ClickAwayListener>
    );
}

export default Property;