import React from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Config from './Config'
import Style from './SideBarMenu/SideBarStyle';
import { makeStyles } from "@material-ui/core/styles";
import { FaMousePointer, FaHandRock } from 'react-icons/fa';
import menuState from './MenuState'

const useStyles = makeStyles(Style);

const SelectionGroupPopup = (props) => {
    const classes = useStyles();

    var {open, anchorEl, placement} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }

    var chooseSelectMode = () => {
        props.setSelectionMode(menuState.menus.selection);
    }

    var chooseMoveMode = () => {
        props.setSelectionMode(menuState.menus.move);
    }

    var itemClass = classes.expandedMenuItem;
    var itemClassActive = classes.expandedMenuItem + ' ' + classes.expandedMenuItemActive
    var selectStyle = itemClass;
    var moveStyle = itemClass;
    if (menuState.selectionGroupVal == menuState.menus.selection) {
        selectStyle = itemClassActive;
    } else {
        moveStyle = itemClassActive;
    }
    var menuClass = classes.expandedMenu;
    const {menuItem} = classes;
    return (
        <div>
            <Popper open={open} anchorEl={anchorEl} placement={placement} transition
            >
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="selection_menu_expanded_paper">
                            <div className={menuClass}>
                                <div className={selectStyle} onClick={chooseSelectMode}>
                                    <div className={menuItem + ' ' + 'sideMenuIcons'}>
                                        <FaMousePointer size="1.2em" color="#000000b8"/>
                                    </div>
                                    <div className={menuItem +' '+ 'menuItems'}>Select</div>
                                </div>
                                <div className={moveStyle} onClick={chooseMoveMode}>
                                    <div className={menuItem + ' ' + 'sideMenuIcons'}>
                                        <FaHandRock size="1.2em" color="#000000b8"/>
                                    </div>
                                    <div className={menuItem +' '+ 'menuItems'}>Move</div>
                                </div>
                            </div>
                         </Paper>
                    </Fade>
                )}
            </Popper>
        </div>
    )
}

export default SelectionGroupPopup;
