import React from 'react'
import { FaCheckCircle } from "react-icons/fa";

const ColorList = (props) => {
    const {colors} = props;
    return colors?.map((color, index)=>{
        var item = null;
        if (index == props.selectedIndex) {
            item = <FaCheckCircle size="2em" color={color.props.color}/>;
        } else {
            item = color;
        }
        return (
            <span onClick={() => props.onColorChange(index, color.props.color)}>
              {item}
            </span>
        )
    })
}

export default ColorList;
