import React, { useState } from "react"
import { AiOutlineClose } from "react-icons/ai"

const Feedback = ({ handleClose }) => {
  const [feedbackForm, setFeedbackForm] = useState({})

  const handleChange = e => {
    const name = e.target.name
    const value = e.target.value
    const newEntry = { [name]: value }
    setFeedbackForm({ ...feedbackForm, ...newEntry })
  }

  const submitForm = () => {
    if (feedbackForm.message) {
      if (window.wsmanager.send) {
        window.wsmanager.send('', JSON.stringify({
          'type': 'feedback',
          'data': feedbackForm
        }));
      }
      handleClose();
    }
  }

  var submitDIsabled = !(feedbackForm.message);

  return (
    <div className="feedback ">
      <div className="feedback-form ">
        <AiOutlineClose
          className="close-feedback"
          onClick={() => handleClose()}
        />
        <h2>Feedback</h2>

        <div className="feedback-input">
          <label htmlFor="name">Name</label>
          <input
            id="name"
            onChange={e => handleChange(e)}
            name="name"
            required
          />
        </div>

        <div className="feedback-input">
          <label htmlFor="email">Email Address</label>
          <input
            id="email"
            onChange={e => handleChange(e)}
            name="email"
            type="email"
            required
          />
        </div>

        <div className="feedback-input">
          <label htmlFor="message">Message</label>
          <textarea
            id="message"
            onChange={e => handleChange(e)}
            name="message"
            type="email"
            required
          />
        </div>

        <button className="submit-feedback" onClick={submitForm} disabled={submitDIsabled}>
          Submit
        </button>
      </div>
    </div>
  )
}

export default Feedback
